import React from 'react'
import SEO from '~/components/seo'
import Form from 'react-bootstrap/Form'
import Button from 'react-bootstrap/Button'
import FormControl from 'react-bootstrap/FormControl'

const ContactPage = () => (
    <>
        <SEO title="Contact" />
        <section className="hero is-dark">
            <Form method="post" action="https://motic-microscopes.myshopify.com/contact#contact_form" id="contact_form" accept-charset="UTF-8" className="contact-form">
                <Form.Group controlId="exampleForm.ControlInput1">
                    <Form.Label>Email address</Form.Label>
                    <Form.Control type="email" placeholder="Email"
                                  autoCorrect="off" autoCapitalize="off" name="contact[email]" />
                </Form.Group>
                <Form.Group controlId="exampleForm.ControlInput1">
                    <Form.Label>Name</Form.Label>
                    <Form.Control type="text" placeholder="Name"
                                  autoCapitalize="words" name="contact[name]" />
                </Form.Group>
                <Form.Group controlId="exampleForm.ControlInput1">
                    <Form.Label>Phone Number</Form.Label>
                    <Form.Control type="tel" placeholder="Phone Number"
                                  autoCapitalize="words" pattern="[0-9\-]*" name="contact[name]" />
                </Form.Group>
                <Form.Group controlId="exampleForm.ControlTextarea1">
                    <Form.Label>Message</Form.Label>
                    <Form.Control as="textarea" rows="3"
                                  id="ContactFormMessage"
                                  name="contact[body]"
                                  placeholder="Message"/>
                </Form.Group>
                <Form.Group>
                    <Button type="submit" className="my-1">
                        Submit
                    </Button>
                </Form.Group>
            </Form>
        </section>
    </>
)


export default ContactPage
